<template>
  <div class="three-canvas"></div>
</template>

<script>
import * as THREE from 'three'
import mixin from './common/common'
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls.js'
import { DragControls } from 'three/examples/jsm/controls/DragControls.js'

export default {
  name: 'ineractiveHover',
  mixins: [mixin],
  props: ['comWidth'],
  data () {
    return {
      mouse: new THREE.Vector2(),
      objects: []
    }
  },
  methods: {
    getRender () {
      const renderer = new THREE.WebGLRenderer({ antialias: true })
      renderer.setPixelRatio(window.devicePixelRatio)
      renderer.setSize(this.width, this.height)
      document.querySelector('.three-canvas').appendChild(renderer.domElement)
      return renderer
    },
    getCamera () {
      const camera = new THREE.PerspectiveCamera(75, this.width / this.height, 1, 10000)
      camera.position.z = 600
      return camera
    },
    getScene () {
      const scene = new THREE.Scene()
      scene.background = new THREE.Color(0xffffff)
      return scene
    },
    getCube (w1 = 10, w2 = 10, w3 = 10) {
      this.geometry = new THREE.BoxGeometry(w1, w2, w3)
      this.material = new THREE.MeshLambertMaterial({color: Math.random() * 0xffffff})
      return new THREE.Mesh(this.geometry, this.material)
    },
    init: function () {
      this.renderer = this.getRender()
      this.camera = this.getCamera()
      this.scene = this.getScene()
      // this.group = new THREE.Group()
      for (let i = 0; i < 100; i++) {
        const cube = this.getCube(30, 30, 30)
        cube.position.set(~~(Math.random() * 500 - 250), ~~(Math.random() * 500 - 250), ~~(Math.random() * 500 - 250))
        cube.rotateX(90 * Math.random() - 45)
        cube.rotateY(90 * Math.random() - 45)
        cube.rotateZ(90 * Math.random() - 45)
        this.scene.add(cube)
        this.objects.push(cube)
      }
      // 环境光
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.8)
      this.scene.add(ambientLight)
      // this.controls = new OrbitControls(this.camera, this.renderer.domElement)
      this.controls = new TrackballControls(this.camera, this.renderer.domElement)
      // this.controls.rotateSpeed = 2.0
      // this.controls.zoomSpeed = 1.8
      // this.controls.panSpeed = 0.8
      // this.controls.noZoom = false
      // this.controls.noPan = true
      // // this.controls.staticMoving = false
      // this.controls.dynamicDampingFactor = 0.3
      this.register()
    },
    animate: function () {
      requestAnimationFrame(this.animate)
      // 鼠标监听
      this.controls.update()
      this.renderer.render(this.scene, this.camera)
    },
    register: function () {
      this.dragControls = new DragControls(this.objects, this.camera, this.renderer.domElement)
      let color = null
      this.dragControls.addEventListener('dragstart', (e) => {
        color = e.object.material.color
        e.object.material.color = new THREE.Color(0xff0000)
        this.controls.enabled = false
      })
      this.dragControls.addEventListener('dragend', (e) => {
        e.object.material.color = color
        // this.scene.remove(e.object)
        color = null
        this.controls.enabled = true
      })
    }
  },
  mounted () {
    this.init()
    this.animate()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  text-align: center;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



